import React, { Component } from "react";
import Zoom from "react-zoom-image";
import logo from "./logo.svg";
import "./App.css";
import grump from "./grump.jpg";

class App extends Component {
  state = { w1: 300, h1: 300 };

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Zoom
            baseImage={{
              src: "build/" + grump,
              width: this.state.w1,
              height: this.state.h1
            }}
            largeImage={{
              src: "build/" + grump,
              width: 500,
              height: 500
            }}
            thumbnailImage={{
              src: "build/" + grump
            }}
            zoomTransitionTime={300}
            mapBorderColor="cyan"
          />
        </header>
      </div>
    );
  }
}

export default App;



